html {
  scroll-behavior: smooth; }


body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Georgia" sans-serif;
  font-weight: regular; }


h1,h2, h3,h4,h5,h6 {
  margin: 0; }

p {
  margin: 0; }

@font-face {
  font-family: "Arial";
  src: url("../assets/fonts/arial/arial-regular.ttf");
  font-weight: regular;
  font-style: normal; }

@font-face {
  font-family: "Arial";
  src: url("../assets/fonts/arial/arial-italic.ttf");
  font-weight: italic;
  font-style: italic; }

@font-face {
  font-family: "Georgia";
  src: url("../assets/fonts/georgia/georgia-regular.ttf");
  font-weight: regular;
  font-style: normal; }

@font-face {
  font-family: "Georgia";
  src: url("../assets/fonts/georgia/georgia-bold.ttf");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: "Georgia";
  src: url("../assets/fonts/georgia/georgia-italic.ttf");
  font-weight: italic;
  font-style: italic; }

@font-face {
  font-family: "Georgia";
  src: url("../assets/fonts/georgia/georgia-bold-italic.ttf");
  font-weight: 600;
  font-style: italic; }

$main-color: #e5e5e5;
$color-light-xl: #ffffff;
$color-primary: #f1cdb3;
$color-primary-light: #fddcc4;
$color-light-s: #fafafa;
