      font-weight: 400;
      font-size: 32px;
      line-height: 110%;
      letter-spacing: 0.06em;
      color: $color-primary;
      margin-top: 0;
      margin-bottom: 10px; }
    &_subtitle {
      display: block;
      font-family: Arial;
      font-weight: 400;
      font-size: 13px;
      letter-spacing: 0.1em;
      color: $color-light-xl;
      margin: 0;
